.section-tumour div.step {
    height: 48px;
    position: relative;
    margin-bottom: 10px;
    float: none;
}

.section-tumour .step > div {
    font-size: 18px;
    height: 48px;
    line-height: 48px;
    color: #FFF;
    background: #cacaca;
    z-index: 1;
    text-align: center;
}

.section-tumour .step > div:before {
    content: '';
    display: block;
    position: absolute;
    left: -24px;
    z-index: 8;
    top: 0;
    border-top: 24px solid #cacaca;
    border-left: 24px solid transparent !important;
    border-bottom: 24px solid #cacaca;
}

.section-tumour .step > div:first-child:before {
    border: none;
}

.section-tumour .step > div:after {
    content: '';
    display: block;
    width: 30px;
    height: 24px;
    position: absolute;
    right: 0;
    z-index: 9;
    top: 0;
    border-top: 24px solid transparent !important;
    border-left: 24px solid #cacaca;
    border-bottom: 24px solid transparent !important;
    background-color: #FFF;
}

.section-tumour .step > div:last-child:after {
    border: none;
    width: 0;
}

.section-tumour .step1 > div:first-of-type {
    background: #feb3ee;

}

.section-tumour .step1 > div:first-of-type:after {
    border-left: 24px solid #feb3ee;
}

.section-tumour .step2 > div:nth-child(2) {
    background: #c072fc;
}

.section-tumour .step2 > div:nth-child(2):before {
    border-bottom: 24px solid #c072fc;
    border-top: 24px solid #c072fc;
}

.section-tumour .step2 > div:nth-child(2):after {
    border-left: 24px solid #c072fc;
}

.section-tumour .step3 > div:nth-child(3) {
    background: #83b5fa;
}

.section-tumour .step3 > div:nth-child(3):before {
    border-bottom: 24px solid #83b5fa;
    border-top: 24px solid #83b5fa;
}

.section-tumour .step3 > div:nth-child(3):after {
    border-left: 24px solid #83b5fa;
}

.section-tumour .step4 > div:nth-child(4) {
    background: #ddfc63;
}

.section-tumour .step4 > div:nth-child(4):before {
    border-bottom: 24px solid #ddfc63;
    border-top: 24px solid #ddfc63;
}

.section-tumour .step4 > div:nth-child(4):after {
    border-left: 24px solid #ddfc63;
    background: #ddfc63;
}

.section-tumour .step5 > div:nth-child(5) {
    background: #98fc54;
}

.section-tumour .step5 > div:nth-child(5):before {
    border-bottom: 24px solid #98fc54;
    border-top: 24px solid #98fc54;
}

.section-tumour .step5 > div:nth-child(5):after {
    border-left: 24px solid #98fc54;
    background: #98fc54;
}

